<template>
    <div class="info">
        <button class="info__close popup-close" @click="$emit('close-popup')"></button>
        <h3 class="info__title">Датчик #{{data.id}}</h3>
        <p class="info__text">{{data.coords}}</p>
        <p class="info__text">Адрес: {{ }}</p>
        <p class="info__text">PM2.5: {{data.data.PM25}}</p>
        <p class="info__text">Уровень загрязнения: 
            <span class="info__level" 
                  :class="{green: level === 'Низкий',
                           yellow: level === 'Средний',
                           red: level === 'Высокий'}">{{level}}</span>
            </p>
    </div>
</template>

<script>
export default {
    props: ['data'],
    computed: {
        level() {
            if (this.data.data.PM25 < 20) {
                return 'Низкий'
            }
            if (this.data.data.PM25 >= 20 && this.data.data.PM25 < 50) {
                return 'Средний'
            }
            if (this.data.data.PM25 >= 50) {
                return 'Высокий'
            }
            return 'none'
        }
    },
    mounted() {
        const url = `https://geocode-maps.yandex.ru/1.x/?apikey=0f35a098-67cb-49e4-a28d-27b3cd6167d0&geocode=55.354775,86.088120&format=json&lang=ru_RU`
        fetch(url)
            .then((response) => {
                return response.json();
            })
            .then((data) => {
                console.log(data);
            });
    }

}
</script>

<style lang="scss" scoped>
.info {
    z-index: 10;
    padding: 20px;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    box-shadow: 0 2px 10px 0 #666666; 
    text-align: left;   
}

.info__title {
    font-size: 20px;
    line-height: 24px;
    margin-top: 5px;
    margin-bottom: 5px;
}

.info__text:not(:first-child) {
    margin-top: 10px;
}
.info__level {
    font-weight: bold;
}
.green {
    color: green;
}

.yellow {
    color: gold;
}
.red {
    color: red;
}
</style>