<template>
<div class="map">
  <yandex-map :settings="settings" :coords="coords" :controls="[]" :zoom="13" :options="{maxZoom:14, minZoom:12}">
    <ymap-marker 
        v-for="item in data" :key="item.id"
        :coords="item.coords" 
        marker-id="123" 
        :icon="{color: setColor(item.data.PM25), content: item.data.PM25}"
        @click="showInfo(item)"
    />
  </yandex-map>
  <InfoPopup v-if="infoPopupIsOpened" :data="infoPopupData" @close-popup="infoPopupIsOpened = false" />
</div>
</template>

<script>
import { yandexMap, ymapMarker } from 'vue-yandex-maps'
import data from '../../public/data'
import InfoPopup from '../components/InfoPopup'
import settings from '../assets/js/ymapSettings'
export default {
  data() {
    return {
      settings: settings,
      coords: [
        55.355669,
        86.088170,
      ],
      data: data,
      infoPopupData: null,
      infoPopupIsOpened: false
    }
  },
  components: { yandexMap, ymapMarker, InfoPopup },
  methods: {
    showInfo(data) {
      this.infoPopupData = data
      this.infoPopupIsOpened = true
    },
    setColor(pm) {
      if (pm < 10) {
        return 'green'
      }
      if (pm >= 10 && pm < 20) {
        return 'olive'
      }
      if (pm >= 20 && pm < 25) {
        return 'yellow'
      }
      if (pm >= 25 && pm < 50) {
        return 'orange'
      }
      if (pm >= 50 && pm < 75) {
        return 'darkOrange'
      }
      if (pm >= 75) {
        return 'red'
      }
    }
  }
 
}
</script>

<style scoped>
.ymap-container {
  height: 100%;
}
</style>
